export default {
	
	data() {
		return {
			nickname:'',
			avatar:'',
			email:'',
			achievement:''
			
		}
	},
	onLoad() {
		if (!uni.getStorageSync("token") && !uni.getStorageSync("SecretKey")) {
			this.$base._isLogin()
		}
		//获取用户信息
		uni.request({
			url: this.baseUrl + "/member-info",
			header: {
				//除注册登录外其他的请求都携带用户token和秘钥
				Authorization: uni.getStorageSync('token')
			},
			success: (res) => {
				console.log(res.data)
				if (this.$base._indexOf(res.data.status)) {
					this.$base._isLogin()
				} else if (res.data.status == 1) {
					this.nickname = res.data.data.NickName
					this.avatar = res.data.data.Avatar
					this.email = res.data.data.Email
					this.achievement = res.data.data.Achievement
				} else {
					uni.showToast({
						title: res.data.message,
						icon: "none"
					})
				}
			}
		})
	
	},
	methods: {
		jumpToBindingPhoneNum() {
			uni.navigateTo({
				url: "./binding-phonenum"
			})
		},
		jumpToSetPassword() {
			uni.navigateTo({
				url: "./authentication?id=6"
			})
		},
		jumpToResetPin() {
			if (this.unBindingPhone == true) {
				uni.showModal({
					title: '未绑定手机号',
					content: '点击确定跳转到绑定手机号',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: "./binding-phonenum"
							})
						} else if (res.cancel) {

						}
					}
				});
			} else {
				uni.navigateTo({
					url: "./authentication?id=1"
				})
			}
		}
	}
}
